#home{
    border: 0px solid white;
    display: flex;
    margin-top: 0px;
 
    margin: auto;
    /* margin-top:56px; */
    justify-content: space-around;
    /* align-items: center; */
    /* animation: fadeIn 5s;
    -webkit-animation: fadeIn 3s;
    -moz-animation: fadeIn 3s;
    -o-animation: fadeIn 3s;
    -ms-animation: fadeIn 2s; */
    position: absolute;
}
@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-moz-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-webkit-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-o-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-ms-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
.about{
    width: 60%;
    border: 0px solid white;
    margin-right: 25px;
    margin-top: 50px;
}
.name{
    color: #ffc400;
}
